
<template>
  <div>
    <div class="block data-picker">
      <el-date-picker type="date" placeholder="选择日期"></el-date-picker>
    </div>
    <el-row :gutter="20">
      <el-col :span="8" class="el-pad">
        <div class="grid-content bg-purple-left">总转诊数量统计(人次)</div>
      </el-col>
      <el-col :span="8" class="el-pad">
        <div class="grid-content bg-purple">上海杨思医院 转诊数量统计(人次)</div>
      </el-col>
      <el-col :span="8" class="el-pad">
        <div class="grid-content bg-purple-right">上海安达医院 转诊数量统计(人次)</div>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <el-col :span="8" class="el-pad">
        <div class="grid-content bg-purple-left">
          <div class="small-font">总转诊量</div>
          <div class="large-number">23,180</div>
          <div class="line"></div>
          <div>
            <el-row class="row-local">
              <el-col :span="12">
                <div class="item-content">
                  <div class="small-font">下转医院1</div>
                  <div class="number">11,097</div>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="item-content">
                  <div class="small-font">下转医院2</div>
                  <div class="number">12,083</div>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
        <div class="echarts-bg">
          <div id="left" class="echarts-content" style="width: 100%;height:100%;"></div>
        </div>
      </el-col>
      <el-col :span="8" class="el-pad">
        <div class="grid-content bg-purple">
          <div class="small-font">上海杨思医院 转诊量</div>
          <div class="large-number">11,097</div>
          <div class="line"></div>
          <div>
            <el-row class="row-local">
              <el-col :span="12">
                <div class="item-content">
                  <div class="small-font">门诊</div>
                  <div class="number">2,287</div>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="item-content">
                  <div class="small-font">住院</div>
                  <div class="number">8,810</div>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
        <div class="echarts-bg">
          <div id="middle" class="echarts-content" style="width: 100%;height:100%;"></div>
        </div>
      </el-col>
      <el-col :span="8" class="el-pad">
        <div class="grid-content bg-purple-right">
          <div class="small-font">上海安达医院 转诊量</div>
          <div class="large-number">12,083</div>
          <div class="line"></div>
          <div>
            <el-row class="row-local">
              <el-col :span="12">
                <div class="item-content">
                  <div class="small-font">门诊</div>
                  <div class="number">3,651</div>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="item-content">
                  <div class="small-font">住院</div>
                  <div class="number">8,432</div>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
        <div class="echarts-bg">
          <div id="right" class="echarts-content" style="width: 100%;height:100%;"></div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<style scoped>
.el-row {
  margin-bottom: 20px;
}
.el-col {
  border-radius: 3px;
}
.bg-purple-left {
  background: #49a2df;
}
.bg-purple {
  background: #e9626b;
}
.bg-purple-right {
  background: #48cbd6;
}
.grid-content {
  border-radius: 4px;
  color: #ffffff;
  font-size: 14px;
  padding: 12px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.el-pad {
  padding-left: 20px !important;
  padding-right: 20px !important;
}

.small-font {
  font-size: 12px;
  color: #ffffff;
}
.large-number {
  font-size: 30px;
  color: #ffffff;
  margin-top: 10px;
}
.number {
  font-size: 24px;
  color: #ffffff;
  margin-top: 10px;
}
.line {
  background: #ffffff;
  height: 0.5px;
  margin: 10px;
}
.item-content {
  color: #ffffff;
  font-size: 14px;
  padding-left: 12px;
  padding-right: 12px;
}
.row-local {
  margin-bottom: 0px;
}
.data-picker {
  text-align: right;
  margin-right: 10px;
  margin-bottom: 10px;
}
.echarts-bg {
  width: 100%;
  background: #ffffff;
  height: 330px;
  margin-top: 20px;
}
.echarts-content {
  padding: 10px;
  box-sizing: border-box;
}
</style>



<script>
export default {
  name: "hello",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  mounted() {
    this.drawLeftPie();
    this.drawMiddlePie();
    this.drawRightPie();
  },
  methods: {
    drawLeftPie() {
      // 基于准备好的dom，初始化echarts实例
      let myLeftChart = this.$echarts.init(document.getElementById("left"));
      // 绘制图表
      myLeftChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          bottom: "bottom",
          left: "left",
          data: ["下转医院1", "下转医院2"]
        },
        series: [
          {
            type: "pie",
            radius: "40%",
            center: ["50%", "50%"],
            data: [
              {
                value: 11097,
                name: "下转医院2",
                itemStyle: { color: "#86cff4" }
              },
              {
                value: 12083,
                name: "下转医院1",
                itemStyle: { color: "#37A2DA" }
              }
            ]
          }
        ]
      });
    },
    drawMiddlePie() {
      // 基于准备好的dom，初始化echarts实例
      let myLeftChart = this.$echarts.init(document.getElementById("middle"));
      // 绘制图表
      myLeftChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          bottom: "bottom",
          left: "left",
          data: ["住院", "门诊"]
        },
        series: [
          {
            type: "pie",
            radius: "40%",
            center: ["50%", "50%"],
            data: [
              { value: 2287, name: "门诊", itemStyle: { color: "#ff9f7f" } },
              { value: 8810, name: "住院", itemStyle: { color: "#e9626b" } }
            ]
          }
        ]
      });
    },
    drawRightPie() {
      // 基于准备好的dom，初始化echarts实例
      let myLeftChart = this.$echarts.init(document.getElementById("right"));
      // 绘制图表
      myLeftChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          bottom: "bottom",
          left: "left",
          data: ["住院", "门诊"]
        },
        series: [
          {
            type: "pie",
            radius: "40%",
            center: ["50%", "50%"],
            data: [
              { value: 3651, name: "门诊", itemStyle: { color: "#97f4f6" } },
              { value: 8432, name: "住院", itemStyle: { color: "#6Ae4e7" } }
            ]
          }
        ]
      });
    }
  }
};
</script>



